@import "../base/settings";

.overlay {
    background-color: white;
    position: fixed;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    opacity: 1;
    top: 0px;
    right: 0px;
    z-index: 1000;
}

.logReg {
    position: fixed;
    left: 50%;
    top: 5%;
    transform: translate(-50%, 0%);
    height: auto;
    width: 50rem;
    background: white 0% 0% no-repeat padding-box;
    border: 1px solid #c5c5c5;
    opacity: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    box-shadow: 4px -1px 50px rgb(194, 194, 194);
    z-index: 2000;

    div {
    }

    input {
        margin-top: 10px;
        margin-bottom: 10px;
    }
}

.logNav {
    height: 23px;
    border: 1px solid rgb(206, 206, 206);
    border-radius: 27px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    box-shadow: 0px 0px 20px rgb(199, 199, 199);
    margin-bottom: 10px;
    width: 60%;
    span {
        padding: 2px 0px;
        text-align: center;
        color: gray;
        font: Regular Helvetica Neue;
        font-size: 1rem;
        cursor: pointer;
    }
    span:hover {
        background-color: $l_pri_color;
        color: white;
    }

    .active {
        background-color: $pri_color;
        color: white;
    }
}

.log {
    border-right: 1px solid white;
    width: 50%;
    border-bottom-left-radius: 27px;
    border-top-left-radius: 27px;
}
.reg {
    width: 50%;
    border-bottom-right-radius: 27px;
    border-top-right-radius: 27px;
}

input {
    background: #ffffff 0% 0% no-repeat padding-box;
    border: 1px solid #707070;
    opacity: 1;
    width: 50%;
    // height: 30px;
    font-size: 0.8rem;
    padding: 5px;
    text-align: center;
}
input:focus {
    outline: none;
    border: 1px solid greenyellow;
}

button {
    padding: 5px;
    width: 30%;
    font-size: 1rem;
    background-color: white;
    border-radius: 27px;
    cursor: pointer;
}
button:active {
    background-color: $l_pri_color;
}
button:focus {
    outline: none;
}

.btnbtn {
    margin-top: 10px;
}

.gotohomebtn {
    height: 20px;
    z-index: 3000;
    cursor: pointer;
}

.login_head {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    color: $pri_color;
    border-bottom: solid 2px $pri_color;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 0.8rem;
    letter-spacing: 1.68px;
    img {
        position: absolute;
        right: 30px;
    }
}

.loadingsvg {
    height: 20px;
    animation-name: rotate;
    animation-duration: 1s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    margin: 0px;
}

@keyframes rotate {
    from {
        transform: rotateZ(0deg);
    }
    to {
        transform: rotateZ(-360deg);
    }
}

.login_body {
    display: flex;
    flex-direction: row;
    width: 100%;
    padding: 30px;
}

.login_body_left {
    border-right: solid 2px $pri_color;
    width: 50%;
    padding: 50px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.google-button {
    height: 40px;
    width: 200px;
    border-width: 0;
    background: white;
    color: #737373;
    border-radius: 5px;
    white-space: nowrap;
    box-shadow: 1px 1px 0px 1px rgba(0, 0, 0, 0.05);
    transition-property: background-color, box-shadow;
    transition-duration: 150ms;
    transition-timing-function: ease-in-out;
    padding: 0;

    &:focus,
    &:hover {
        box-shadow: 1px 4px 5px 1px rgba(0, 0, 0, 0.1);
    }

    &:active {
        background-color: #e5e5e5;
        box-shadow: none;
        transition-duration: 10ms;
    }
}

.google-button__icon {
    display: inline-block;
    vertical-align: middle;
    margin: 8px 0 8px 8px;
    width: 18px;
    height: 20px;
    box-sizing: border-box;
}
.google-button__text {
    display: inline-block;
    vertical-align: middle;
    padding: 0 24px;
    font-size: 14px;
    font-weight: bold;
    font-family: "Roboto", arial, sans-serif;
}

.login_body_right {
    // border: solid 1px black;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding-bottom: 50px;
    button {
        margin: 10px;
    }
}
